"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { ConversationList } from "@/components/conversation-list"
import { motion } from "framer-motion"
import { storage } from "@/lib/storage"
import type { AuthUser, Conversation } from "@/lib/types"
import Link from "next/link"
import { LogOut, Settings } from "lucide-react"

export default function DashboardPage() {
  const router = useRouter()
  const [auth, setAuth] = useState<AuthUser | null>(null)
  const [conversations, setConversations] = useState<Conversation[]>([])
  const [loading, setLoading] = useState(true)

  useEffect(() => {
    const authUser = storage.getAuth()
    if (!authUser) {
      router.push("/login")
      return
    }
    setAuth(authUser)
    loadConversations(authUser.id)
  }, [router])

  const loadConversations = (businessId: string) => {
    const convs = storage.getConversationsByBusinessId(businessId)
    setConversations(convs)
    setLoading(false)
  }

  const handleLogout = () => {
    storage.clearAuth()
    router.push("/")
  }

  if (!auth) return null

  return (
    <main className="h-screen flex flex-col md:flex-row bg-background">
      {/* Sidebar */}
      <motion.aside
        initial={{ opacity: 0, x: -20 }}
        animate={{ opacity: 1, x: 0 }}
        className="w-full md:w-80 border-r border-border flex flex-col bg-card"
      >
        {/* Header */}
        <div className="p-4 border-b border-border">
          <div className="flex items-center justify-between mb-4">
            <h1 className="text-2xl font-bold text-primary">Leenk</h1>
            <div className="flex gap-2">
              <Link href="/dashboard/settings">
                <Button variant="ghost" size="sm">
                  <Settings className="w-4 h-4" />
                </Button>
              </Link>
              <Button variant="ghost" size="sm" onClick={handleLogout}>
                <LogOut className="w-4 h-4" />
              </Button>
            </div>
          </div>
          <div className="text-sm">
            <p className="font-medium">{auth.business?.businessName}</p>
            <p className="text-muted-foreground text-xs">{auth.email}</p>
          </div>
        </div>

        {/* Conversations List */}
        <div className="flex-1 overflow-y-auto">
          {loading ? (
            <div className="flex items-center justify-center h-full">
              <p className="text-muted-foreground">Loading conversations...</p>
            </div>
          ) : (
            <ConversationList conversations={conversations} />
          )}
        </div>

        {/* Share Link */}
        {auth.business?.phone && (
          <div className="p-4 border-t border-border bg-secondary/20">
            <p className="text-xs text-muted-foreground mb-2">Share this link with customers:</p>
            <code className="text-xs bg-input p-2 rounded block truncate">/chat/{auth.business.phone}</code>
          </div>
        )}
      </motion.aside>

      {/* Main Content */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        className="flex-1 flex items-center justify-center p-4"
      >
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-2">Select a Conversation</h2>
          <p className="text-muted-foreground">Click on a conversation to start chatting with your customers</p>
        </div>
      </motion.div>
    </main>
  )
}
